import React from 'react';
import classes from './Homepage.module.css'
import i18n from '../../i18n';
import {Col, Container, Row} from "react-bootstrap";
import * as Svg from '../../Svg';

const SixthSection = () => {
    return (
        <div className={classes.sixthSection}>
            <div className={classes.contentWrap}>
                <Container fluid={true}>
                    <Row>
                        <Col xs={12}>
                            <h2 className={classes.title}>
                                {i18n.t('Contacts')}
                            </h2>
                        </Col>
                    </Row>
                    <div className={classes.contentBlock}>
                        <Row>
                            <Col xs={5}>
                                <img src="/images/contactsSection.jpg" alt="contacts"/>
                            </Col>
                            <Col xs={7}>
                                <div>
                                    <iframe
                                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d20338.109740085063!2d30.52906487440668!3d50.417557997514336!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x40d4cf6bf1e14ac7%3A0x6276ea5f5b14bfa2!2zMzJBLCDQsdGD0LsuINCU0YDRg9C20LHRiyDQndCw0YDQvtC00L7QsiwgMzLQkCwg0JrQuNC10LIsIDAyMDAw!5e0!3m2!1sru!2sua!4v1590686374693!5m2!1sru!2sua"
                                        width="100%" height="300" frameBorder="0" style={{border:0}} allowFullScreen="" title={i18n.t('Kiyev, b. Druzhby Narodov 32a')}
                                        aria-hidden="false" tabIndex="0"></iframe>
                                </div>
                                <div className={classes.infoBlock}>
                                    <div>
                                        <Svg.TimeIco/>
                                        <p>{i18n.t('Schedule')}: 10:00 - 22:00</p>
                                    </div>
                                    <div>
                                        <Svg.PointIco/>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </div>
                </Container>
            </div>
        </div>
    )
};
export default SixthSection;